<template>
  <!-- <span>#facade</span> -->
  <div>
    <header class="cf pd-h5 h45 border-box fixed top0 left0 w100p bg-fff txt-c z-idx2" style="background: #f7f00f">
      <img src="../assets/img/solomon.svg" class="v-top" height="45" />
      <strong class="inline-block h100p line-h45px v-top">솔로몬의 하루</strong>
      <div class="absolute top0 right0 h100p w45 txt-c pd-t10 border-box" @click="getAdminPop(1);">
        <img src="../assets/img/admin_on.svg" height="25" />
      </div>
    </header>
    <footer class="fixed bottom0 left0 w100p h50 bg-fff border-t-eee border-box z-idx2 flex flex-row"
    style="background1: linear-gradient(to bottom, transparent, #fff)">
        <span id="btn_rank" class="flex flex-col flex1 txt-c pd-t12">
            <span ref="rank" @click="slideTo(0);">
                <img src="../assets/img/rank_on.svg" height="26" v-if="state.swiperIdx == 0" />
                <img src="../assets/img/rank_off.svg" height="26" v-else data-on-off="off" />
            </span>
        <!-- <div>Icons made by
                <a href="https://www.flaticon.com/authors/smashicons" title="Smashicons">Smashicons</a> from
                <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a>
            </div> -->
      </span>
      <span id="btn_home" class="flex flex-col flex1 txt-c pd-t12">
          <span ref="home" @click="slideTo(1);">
                <img src="../assets/img/home_on.svg" height="26" v-if="state.swiperIdx == 1" />
                <img src="../assets/img/home_off.svg" height="26" v-else />
          </span>
        <!-- <div>Icons made by
                <a href="https://www.flaticon.com/authors/smashicons" title="Smashicons">Smashicons</a> from
                <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a>
            </div> -->
      </span>
      <span id="btn_myPage" class="flex flex-col flex1 txt-c pd-t12">
          <span ref="userInfo" @click="slideTo(2);">
                <img src="../assets/img/user_on.svg" height="26" v-if="state.swiperIdx == 2" />
                <img src="../assets/img/user_off.svg" height="26" v-else />
          </span>
        <!-- <div>Icons made by
                <a href="https://www.flaticon.com/authors/smashicons" title="Smashicons">Smashicons</a> from
                <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a>
            </div> -->
      </span>
    </footer>
    <div class="fixed top0 left0 right0 bottom0 bg-000-0_5 z-idx2 pd10" v-if="state.isPopup">
      <div class="vertical-m mx-h100p bg-fff w80p mg0auto media-mx-w767-w100p">
        <div class="h30 cf" style="background:#f7f00f;">
          <div class="float-r h30 w30 font30 txt-c line-h30px color-fff" @click="closePop();">&#10799;</div>
        </div>
        <AdminPop v-if="state.isAdminPop"/>
        <ResultPop v-if="state.isResultPop"/>
      </div>
    </div>
    <div class="pd-t45 pd-b50 h100vh border-box"
    style="background:linear-gradient(-15deg, #ffffcc80 30%, transparent 0), linear-gradient(10deg, #ffffcc80 50%, transparent 0);">
      <div class="swiper-container h100p" ref="swiperRef">
        <div class="swiper-wrapper">
          <div class="swiper-slide overflow-y-auto"><MainRanking /></div>
          <div class="swiper-slide overflow-y-auto"><MainHome /></div>
          <div class="swiper-slide overflow-y-auto"><MainUserInfo /></div>
        </div>
      </div>
    </div>
  </div>

  <!-- <span>{{ store.state.number }}</span> -->
  <!-- <button @click="increase()">Click me</button>
<button @click="post()">Post</button> -->
  <!-- <ul>
  <li v-for="(t, idx) in state.todos" :key="idx">{{ t.title }}</li>
</ul> -->
  <!-- <input type="text" ref="inputRef" /> -->
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import { onMounted } from "@vue/runtime-core";
import Swiper from "swiper";

import MainHome from "./MainHome";
import MainUserInfo from "./MainUserInfo";
import MainRanking from "./MainRanking";
import AdminPop from "./AdminPop";
import ResultPop from "./ResultPop";

// import { useStore } from "vuex";
// import axios from "axios";

export default {
    components: {
        MainHome,
        MainUserInfo,
        MainRanking,
        AdminPop,
        ResultPop,
    },
    setup() {
    // const store = useStore();
        const state = reactive({
            todos: [],
            swiper: null,
            swiperIdx: 1,
            isPopup: false,
            isAdminPop: false,
            isResultPop: false
        });

        const slideTo = (pageIdx) => {
            state.swiper.slideTo(pageIdx, 500);
            state.swiperIdx = pageIdx;
        }

        const getAdminPop = (boolean) => {
          state.isPopup = true;
          state.isAdminPop = boolean ? true : false;
        }

        const closePop = () => {
          state.isPopup = false;
        }

        //ref
        const swiperRef = ref(null);
        //ref

        // const increase = () => {
        //   store.commit("increase", 1);
        // };

        // const post = () => {
        //   axios.get("https://jsonplaceholder.typicode.com/todos").then((res) => {
        // state.todos = res.data;
        //   });

        // };

        onMounted(() => {
            console.log("mounted");
            state.swiper = new Swiper(swiperRef.value, {
                on: {
                    activeIndexChange: function () {state.swiperIdx = this.realIndex}
                }
            });

            state.swiper.slideTo(state.swiperIdx, false, false);
        });

        return {
            //var
            state,
            //var

            //ref
            swiperRef,
            //ref

            //method
            slideTo,
            getAdminPop,
            closePop,
            //method

            //const
            //const


        //   store,
        };
    }
};
</script>